import { makeStyles } from "@material-ui/core";

export const useStyles = makeStyles(() => ({
    container: {
        backgroundColor: "#f0f0f0",
        display: "flex",
        justifyContent: "center",
        padding: "150px 0px 30px",
    },
    paper: {
        padding: "80px 100px 20px",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        width: 500,
        background: "white",
        position: "relative",
        "&>img": {
            width: 200,
            position: "absolute",
            top: "-40px",
            userDrag: "none",
            userSelect: "none"
        }
    },
    btn: {
        width: "100%",
        border: "1px solid #C4C4C4 !important",
        background: "white",
        color: "black",
        marginBottom: 20,
        paddingBottom: 10
    }
}))