import React from 'react'
import notFound from "assets/icons/notFound.png";
import Header from 'components/Header';
import Footer from 'components/Footer';
import { useStyles } from './notFound.style';
import { Typography } from '@material-ui/core';
import browserHistory from 'browser-history/browser-history';

export const NotFound = () => {
    const c = useStyles();
    return (
        <>
            <Header />
            <div className={c.container}>
                <div className={c.paper}>
                    <img src={notFound} />
                    <Typography variant="h4" style={{ fontWeight: 600, padding: "10px 0px" }}>Well this is embarrassingâ€¦</Typography>
                    <Typography style={{ fontSize: 14 }}>The page you were looking for is not here. If you clicked on a link within our site, please report that broken link back to us, and we'll make sure to fix it right away.</Typography>
                    <Typography style={{ alignSelf: "flex-start", fontSize: 14, margin: "20px 0px 40px" }}>Thank you!</Typography>
                    <button className={`btn-primary btn-sm ${c.btn}`}
                        onClick={() => browserHistory.push("/")}>
                        GO TO THE HOMEPAGE
                    </button>
                    <Typography style={{ color: "#88D5ED", fontSize: 13, cursor: 'pointer' }}>Contact support team</Typography>
                </div>
            </div>
            <Footer />
        </>
    )
}